<template>
  <div class="about-view">
    <div class="my-button">
      <button id="btn" @click="drawer = true">Click Me</button>
    </div>
    <div id="myImage" class="about-view__img-content" :style="[{width:imgWidth+'px',height:imgHeight+'px'}]">
      <img :src="backgroundImg"/>
      <img style="width: 100px;position: absolute;z-index: 99" :src="logo" :class="imgPosition"/>
      <span class="about-view__font">{{ dateTime }}</span>
    </div>
    <div v-if="!backgroundImg">
      <div class="g-container">
        <div class="g-top">
          <div class="line"></div>
        </div>
        <div class="g-bottom"></div>
        <p>用心发现身边的美 <span>爱</span>摄影 爱生活</p>
      </div>
      <svg>
        <filter id="fractal" filterUnits="objectBoundingBox" x="0%" y="0%" width="100%" height="100%">
          <feTurbulence id="turbulence" type="fractalNoise" baseFrequency="0.005 0.005" numOctaves="10">
            <animate
                attributeName="baseFrequency"
                dur="30s"
                values="0.005 0.005;0.05 0.3;0.005 0.005"
                repeatCount="indefinite"/>
          </feTurbulence>
          <feDisplacementMap in="SourceGraphic" scale="15"></feDisplacementMap>
        </filter>
      </svg>
    </div>
    <el-drawer
        title="添加图片水印"
        :visible.sync="drawer"
        direction="rtl">
      <div style="margin-left: 15px">
        <el-form ref="form" :model="form" label-width="100px">
          <el-form-item label="下载图片">
            <el-row>
              <el-button size="mini" type="primary" @click="saveImage" icon="el-icon-download">下载图片</el-button>
            </el-row>
          </el-form-item>
          <el-form-item label="上传图片">
            <el-row>
              <el-upload
                  ref="upload"
                  accept="image/jpeg,image/bmp,image/jpg,image/png,image/tif,image/gif,image/pcx,image/tga,image/exif,image/fpx,image/svg,image/psd,image/cdr,image/pcd,image/dxf,image/ufo,image/eps,image/ai,image/raw,image/WMF,image/webp,image/avif,image/apng"
                  action="#"
                  :show-file-list="false"
                  class="avatar-uploader"
                  :headers="headers"
                  :auto-upload="false"
                  :file-list="fileList"
                  :on-change="handleChange"
              >
                <i slot="default" class="el-icon-plus"></i>
              </el-upload>
            </el-row>
          </el-form-item>
          <el-form-item label="水印位置">
            <el-row>
              <el-radio-group v-model="logoPosition" @change="logoPositionChange">
                <el-radio :label="1">左上</el-radio>
                <el-radio :label="2">左下</el-radio>
                <el-radio :label="3">右上</el-radio>
                <el-radio :label="4">右下</el-radio>
              </el-radio-group>
            </el-row>
          </el-form-item>
          <el-form-item label="水印">
            <el-row>
              <el-select
                  v-model="logo"
                  placeholder="请选择水印">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-row>
          </el-form-item>
          <el-form-item label="ccd数字时间">
            <el-row>
              <el-radio-group v-model="timePosition" @change="timePositionChange">
                <el-radio :label="1">隐藏</el-radio>
                <el-radio :label="2">显示</el-radio>
              </el-radio-group>
            </el-row>
          </el-form-item>
          <el-form-item label="日期时间" v-if="timePosition ==2">
            <el-row>
              <el-date-picker
                  @change="datePickerChange"
                  v-model="dateTime"
                  type="datetime"
                  placeholder="选择日期时间">
              </el-date-picker>
            </el-row>
          </el-form-item>
        </el-form>
      </div>
    </el-drawer>
    <a id="link"></a>
  </div>
</template>
<script>
import html2canvas from "html2canvas";
import moment from 'moment'

export default {
  name: 'AboutView',
  data() {
    return {
      form: {},
      backgroundImg: '',
      fileList: [],
      drawer: false,
      imgWidth: '',
      imgHeight: '',
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      imgSrc: '',
      options: [
        {
          value: 'data:image/png;base64,',
          label: '宾得'
        }, {
          value: 'data:image/png;base64,',
          label: '索尼'
        }, {
          value: 'data:image/png;base64,',
          label: '松下'
        }, {
          value: 'data:image/png;base64,',
          label: '哈苏'
        }, {
          value: 'data:image/png;base64,',
          label: '富士'
        }, {
          value: 'data:image/png;base64,',
          label: '佳能'
        }, {
          value: 'data:image/png;base64,',
          label: '尼康'
        }, {
          value: 'data:image/png;base64,',
          label: '奥林巴斯'
        }, {
          value: 'data:image/png;base64,',
          label: '理光'
        }, {
          value: 'data:image/png;base64,',
          label: '徕卡'
        }],
      logo: '',
      logoPosition: 1,
      timePosition: 1,
      imgPosition: 'img-position__left-top',
      dateTime: '',
    }
  },
  mounted() {
    this.btncss()
  },
  methods: {
    btncss(){
      let btn = document.getElementById("btn");
      btn.forEach((btn) => {
        btn.onclick = function (e) {
          let x = e.pageX - e.target.offsetLeft;
          let y = e.pageY - e.target.offsetTop;
          let ripples = document.createElement("span");

          ripples.style.left = x + "px";
          ripples.style.top = y + "px";
          this.appendChild(ripples);

          setTimeout(() => {
            ripples.remove();
          }, 2000); /*1000 = 1s*/
        };
      });
    },
    handleChange(file, fileList) {
      this.backgroundImg = ''
      this.fileList = fileList
      this.fileName = file.name
      this.backgroundImg = URL.createObjectURL(fileList[this.fileList.length - 1].raw);
      this.imgchecked(file)
      const isLt2M = (file.size / 1024 / 1024 < 100)
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 100MB!')
        this.fileList.pop()
      }
      return isLt2M
    },
    imgchecked(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader()
        reader.readAsDataURL(file.raw) // 必须用file.raw
        reader.onload = () => {
          // 让页面中的img标签的src指向读取的路径
          let img = new Image()
          img.src = reader.result

          img.onload = () => {
            this.imgWidth = img.width
            this.imgHeight = img.height
          }
        }
      })
    },
    saveImage() {
      html2canvas(document.querySelector("#myImage")).then((canvas) => {
        let image = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream");
        let link = document.getElementById("link");
        link.setAttribute("download", `${this.fileName}.png`);
        link.setAttribute("href", canvas.toDataURL("image/png").replace("image/png", "image/octet-stream"));
        link.click();
      });
    },
    logoPositionChange(val) {
      switch (val) {
        case 1:
          this.imgPosition = 'img-position__left-top';
          break
        case 2:
          this.imgPosition = 'img-position__left-bottom';
          break
        case 3:
          this.imgPosition = 'img-position__right-top';
          break
        default:
          this.imgPosition = 'img-position__right-bottom';

      }
    },
    timePositionChange(val) {
      if (val == 1) this.dateTime = ''
    },
    datePickerChange() {
      this.dateTime = moment(this.dateTime).format('YYYY MM DD HH:MM:SS')
    }
  }
}
</script>
<style lang="less" scoped>
.about-view {
  .about-view__img-content {
    position: relative;
  }

  .about-view__logo-wrap {
    position: absolute;
    bottom: 0;
    z-index: 1;
  }

  .avatar-uploader {
    margin-top: 10px;
    width: 100px;
    height: 100px;
    border: 1px solid #eee;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 50px;
  }

  .img-position__left-top {
    top: 15px;
    left: 15px;
  }

  .img-position__left-bottom {
    bottom: 15px;
    left: 15px;
  }

  .img-position__right-top {
    top: 20px;
    right: 15px;
  }

  .img-position__right-bottom {
    bottom: 45px;
    right: 15px;
  }

  .about-view__font {
    font-family: DigifaceWide;
    color: rgb(231, 129, 31);
    font-size: 18px;
    position: absolute;
    right: 10px;
    bottom: 20px;
  }

  @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;1,200&display=swap');
  body, html {
    width: 100%;
    height: 100%;
    display: flex;
    background: #000;
    font-family: 'Montserrat', sans-serif;
  }
  .g-container {
    margin: auto;
  }
  p {
    position: fixed;
    bottom: calc(50vh - 150px);
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 26px;
    color: #fff;
    text-align: center;
    margin-top: 12px;

    span {
      display: inline-block;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        width: 18px;
        left: 50%;
        transform: translate(-50%, 4px);
        border-bottom: 4px solid #2d60aa;
      }
    }
  }
  svg {
    position: absolute;
  }
  .g-top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 50vh;
    background: #000;
    overflow: hidden;
    z-index: 10;

    &::before {
      content: "";
      position: absolute;
      border-radius: 50%;
      bottom: 0;
      left: 50%;
      width: 200px;
      height: 200px;
      transform: translate(-50%, 100px);
      box-sizing: border-box;
      background: #000;
      border: 25px solid #fff;
      z-index: 1;
      box-shadow:
          0 0 4px 1px rgba(255, 255, 255, .8),
          0 0 8px 2px rgba(255, 255, 255, .6);
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: repeating-radial-gradient(circle at 50% 100%, transparent, transparent 5px, #3a77f6 5.2px, #3a77f6 6.2px, transparent 6.5px);
      mask: radial-gradient(circle at 50% 100%, rgba(255, 255, 255, 1), transparent 25%, transparent);
      z-index: 0;
    }
  }
  .g-bottom {
    position: fixed;
    top: 50vh;
    left: 0;
    width: 100vw;
    height: 50vh;
    background: #000;
    overflow: hidden;
    filter: url(#fractal);

    &::before {
      content: "";
      position: absolute;
      border-radius: 50%;
      top: 0;
      width: 200px;
      height: 200px;
      background: #000;
      left: 50%;
      transform: translate(-50%, -100px);
      box-sizing: border-box;
      border: 25px solid #fff;
      z-index: 2;
      box-shadow:
          0 0 4px rgba(255, 255, 255, .8),
          0 0 8px rgba(255, 255, 255, .7),
          0 0 20px rgba(255, 255, 255, .6);
      filter: blur(4px);
      mask: linear-gradient(rgba(255, 255, 255, 1), rgba(255, 255, 255, 1) 50%, rgba(255, 255, 255, .9));
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: repeating-radial-gradient(circle at 50% 0, transparent, transparent 5px, #3a77f6 5.1px, #3a77f6 6.4px, transparent 6.5px);
      mask: radial-gradient(circle at 50% 0, rgba(255, 255, 255, .6), transparent 25%, transparent);
      z-index: 3;
    }
  }
  .line {
    position: absolute;
    bottom: -8px;
    left: 50%;
    width: 500px;
    height: 6px;
    border-radius: 50%;
    background: rgba(46,107,232, 0.8);
    transform: translate(-50%, -50%);
    z-index: 10;
    filter: blur(5px) contrast(1.2) brightness(1.2);
    box-shadow:
        0 0 5px rgba(46,107,232, 0.7);
  }

  .my-button{
    position: fixed;
    top: 0;
    z-index: 999;
    margin-top: 20px;
    margin-left: 20px;
    button {
      position: relative;
      overflow: hidden;
      background: #42455a;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      border: none;
      font-size: 1rem;
      color: #e0ffff;
      font-weight: 500;
      cursor: pointer;
      z-index: 1;
    }
    button:active{
      transform: scale(0.95);
    }
    button::before{
      content: '';
      position: absolute;
      inset: -1px 30px;
      background: #00c2cb;
      transition: 500ms;
      animation: rotate 4s linear infinite;
      z-index: -1;
    }
    button:hover::before{
      inset: -1px;
    }
    button::after{
      content: '';
      position: absolute;
      inset: 3px;
      border-radius: 50%;
      background: #22232e;
      z-index: -1;
    }
    @keyframes rotate {
      0%{
        transform: rotate(0deg);
      }
      100%{
        transform: rotate(360deg);
      }
    }
  }
}
</style>
